@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
}
.visually-hidden:not(:focus):not(:active),
input[type="radio"],
input[type="checkbox"] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
@media (min-width: 1200px) {
  .body-wraper {
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.nav {
  color: #ffffff;
  background-color: #82b3d3;
}
.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nav__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.nav__link {
  font-size: 18px;
  line-height: 40px;
  font-weight: 700;
  display: block;
  padding: 8px 0;
  text-align: center;
  color: #ffffff;
}
.nav__link:hover,
.nav__link:focus {
  background-color: #669ec1;
}
.nav__link:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5596be;
}
.nav__button {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.nav--closed .nav__button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 3px;
  background-color: #82b3d3;
  box-shadow: 0 8px 0 0 #82b3d3, 0 16px 0 0 #82b3d3;
}
.nav--open .nav__button {
  background-color: transparent;
}
.nav--open .nav__button::before,
.nav--open .nav__button::after {
  content: "";
  position: absolute;
  top: 9px;
  left: -3px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.nav--open .nav__button::before {
  transform: rotate(45deg);
}
.nav--open .nav__button::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .nav--closed .nav__list {
    display: none;
  }
  .nav--nojs .nav__button {
    display: none;
  }
  .nav--nojs .nav__list {
    display: flex;
  }
}
@media (min-width: 768px) {
  .nav {
    color: #000000;
    background-color: #ffffff;
  }
  .nav__list {
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  .nav__item {
    box-shadow: none;
    min-width: 153px;
    flex-grow: 1;
  }
  .nav__item:nth-child(2) {
    margin-right: 71px;
  }
  .nav__item:nth-child(3) {
    margin-left: 71px;
  }
  .nav__link {
    color: #000000;
    background-color: #ffffff;
    font-size: 14px;
    line-height: 26px;
    font-weight: 700;
    padding: 15px 0;
  }
  .nav__link:hover,
  .nav__link:focus {
    color: #82b3d3;
    background-color: #ffffff;
  }
  .nav__link:active {
    opacity: 0.3;
  }
  .nav__link--active {
    color: #766357;
  }
  .nav__link--active:hover,
  .nav__link--active:focus {
    color: #604e43;
  }
  .nav__button {
    display: none;
  }
}
@media (min-width: 1200px) {
  .nav__item {
    box-shadow: none;
    width: 239px;
  }
